import React from "react";
import {Button, Modal} from "react-bootstrap";
import {Link} from "react-router-dom";

function BadTuyaCredentialsErrorModal({showBadTuyaCredentialsErrorModal, setShowBadTuyaCredentialsErrorModal}) {

    return (
        <Modal show={showBadTuyaCredentialsErrorModal} onHide={setShowBadTuyaCredentialsErrorModal}>
            <Modal.Header closeButton>
                <Modal.Title>  </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                     IOT Tuya.    <Link to="/user/profile/">
                </Link> 
            </Modal.Body>
            <Modal.Footer>
                <Link variant="primary" to="/user/profile/" className={"btn btn-primary"}><i className="bi bi-person-fill"></i> 
                    </Link>
                <Button variant="secondary" onClick={()=>{setShowBadTuyaCredentialsErrorModal(false)}}></Button>
            </Modal.Footer>
        </Modal>
    );
}

export default BadTuyaCredentialsErrorModal;